:root {
  --color-main: #222;
  --color-font: #ddd;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: var(--color-font);
  background: var(--color-main);
}

input[type=range] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  width: 100%;
}

input[type=range]:focus {
  outline: none;
}


