.schemes {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  grid-area: 3 / 2;
  margin-bottom: 1em;
}

.scheme__label {
}

@media (min-width: 600px) {
  .schemes {
    grid-area: 2 / 1 / 2 / 3;
  }
}

@media (min-width: 900px) {
  .schemes {
    grid-area: 2 / 2;
  }
}



