.swatches {
  grid-area: 4 / 2;
}

.swatches__samples {
  display: -ms-flexbox;
  display: flex;
  width: 1fr;
  height: 2rem;
}

.samples__color {
  width: 100%;
  height: 100%;
}

@media (min-width: 600px) {
  .swatches {
    grid-area: 1 / 3 / 3 / 3;
  }
  .swatches__samples {
    height: 4rem;
  }
}



